.custom_table {
  &.scrollable {
    .MuiTable-root:first-child {
      width: 2400px;
    }
  }

  .toolbar {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .MuiToolbar-root {
      .MuiTypography-root {
        margin-right: 24px;
      }

      & > div:last-child > div > div {
        display: flex;
      }
    }


    .start_end_date_picker {
      padding: 24px 24px;
    }
  }
}