@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,500,700&display=swap&subset=korean');
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  font-size: 16px;
  font-family: 'Noto Sans KR', sans-serif;
}

body, html, input, textarea, select, button {
  font-family: 'Noto Sans KR', sans-serif;
}

.height-100 {
  height: 100%;
}

.systemAccountModify .MuiFormControl-root,
.orgAccountModify .MuiFormControl-root,
.memberAccountView .MuiFormControl-root,
.orgAccountDialog .MuiFormControl-root, 
.systemAccountDialog .MuiFormControl-root {
  width: 100%;
}

.c-table {
  width: 100%;
  border-spacing: 0;
}

.c-table thead th {
  padding: 1em 0;
  border-bottom: 1px solid #ccc;
}

.c-table tbody tr td {
  text-align: center;
}